/* Global Styles */
:root {
  --primary-color: #dc3545;
  --primary-color: green;
  --dark-color: #333333;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
}
.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: var(--primary-color);
  color: #fff;
}

.navbar i {
  color: yellow;
}

.navbar-toggler {
  color: rgb(224, 15, 15);
  /* background-color: darkgray; */
}

.navbar-toggler-icon {
  color: #000;
  background-color: darkgray;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding: 0.7rem 2rem; */
  z-index: 1;
  width: 100%;
  /* opacity: 0.9; */
  /* margin-bottom: 1rem; */
  position: fixed;
}

.navbar ul {
  display: flex;
}

.navbar a {
  color: #fff;
  padding: 0.45rem;
  margin: 0 0.25rem;
}

.navbar a:hover {
  color: var(--light-color);
}

.navbar .welcome span {
  margin-right: 0.6rem;
}
